html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

.gigantic, .huge, .large, .bigger, .big,
h1, h2, h3, h4, h5, h6 {
  color: #BD5248;
  font-weight: bold; }

.gigantic {
  font-size: 11rem;
  line-height: 1.09;
  letter-spacing: -2px; }

.huge, h1 {
  font-size: 6.8rem;
  line-height: 1.05;
  letter-spacing: -1px; }

.large, h2 {
  font-size: 4.2rem;
  line-height: 1.14; }

.bigger, h3 {
  font-size: 2.6rem;
  line-height: 1.38; }

.big, h4 {
  font-size: 2.2rem;
  line-height: 1.38; }

.small, small {
  font-size: 1rem;
  line-height: 1.2; }

p {
  margin: 0 0 2rem 0; }

em {
  font-style: italic; }

strong {
  font-weight: bold; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 1rem 0 3rem;
  height: 0; }

a {
  color: #BD5248;
  text-decoration: none;
  outline: 0; }

a:hover, a:focus {
  color: #d89993; }

::selection {
  background: #FFF498; }

::-moz-selection {
  background: #FFF498; }

img::selection {
  background: transparent; }

img::-moz-selection {
  background: transparent; }

body {
  -webkit-tap-highlight-color: #FFF498; }

html,
body {
  min-height: 100vh; }

body {
  background: #F6F6F8;
  font-size: 1.6rem;
  line-height: 1.6;
  font-family: 'PT Sans', sans-serif;
  color: #2E2D33;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }

.header {
  padding: 2rem 0rem 1.7rem;
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.4); }
  .header-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 128rem;
    margin-left: auto;
    margin-right: auto; }
  .header__logo-link {
    display: block;
    width: 7.5rem;
    height: 7.5rem;
    margin-left: 3rem; }
    @media (min-width: 45em) {
      .header__logo-link {
        margin-left: 1rem; } }
    @media (min-width: 80.0625em) {
      .header__logo-link {
        margin-left: 0; } }

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: all .5s;
  pointer-events: none; }
  @media (min-width: 45em) {
    .nav__list {
      opacity: 1;
      pointer-events: all;
      position: static;
      display: flex; } }

.nav__link {
  display: block;
  padding: 1rem 0;
  font-size: 2.7rem;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.9);
  color: #ccc; }
  @media (min-width: 45em) {
    .nav__link {
      font-size: 1.8rem;
      background: none;
      color: #222;
      font-weight: 400;
      text-transform: uppercase;
      padding: 1rem 1.4rem; } }
  @media (min-width: 57.5em) {
    .nav__link {
      font-size: 2.4rem;
      padding: 1rem 1.8rem; } }
  .nav__link:hover, .nav__link:focus {
    color: white;
    background: rgba(0, 0, 0, 0.8); }

.nav__btn.open + .nav__list {
  opacity: 1;
  pointer-events: all; }

.nav__btn {
  width: 6.5rem;
  height: 6rem;
  display: block;
  background: rgba(0, 0, 0, 0.4);
  border: none;
  border-radius: .8rem;
  padding: .3rem .5rem;
  position: fixed;
  top: 2.6rem;
  right: 3.6rem;
  overflow: hidden;
  z-index: 100; }
  @media (min-width: 45em) {
    .nav__btn {
      display: none; } }
  .nav__btn-bars {
    display: block;
    height: .5rem;
    background: rgba(250, 250, 250, 0.7);
    position: relative;
    transition: all .3s ease-in-out; }
    .nav__btn-bars::before, .nav__btn-bars::after {
      content: "";
      display: block;
      height: .5rem;
      background: rgba(250, 250, 250, 0.7);
      position: relative;
      transition: all .3s ease-in-out; }
    .nav__btn-bars::before {
      top: -1.6rem; }
    .nav__btn-bars::after {
      top: 1.1rem; }
  .nav__btn.open {
    background: rgba(244, 244, 244, 0.7); }
  .nav__btn.open .nav__btn-bars {
    background: transparent; }
    .nav__btn.open .nav__btn-bars::before {
      transform: rotate(315deg);
      background: rgba(0, 0, 0, 0.7);
      top: 0; }
    .nav__btn.open .nav__btn-bars::after {
      transform: rotate(-315deg);
      background: rgba(0, 0, 0, 0.7);
      top: -.5rem; }

.hide-text {
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden; }

.home-wrap {
  max-width: 128rem;
  margin-left: auto;
  margin-right: auto; }

.flex {
  display: flex; }
